const newLocal = '/';

export const getTeachingLevel = (difficulty) => {
  switch (difficulty) {
    case 'E': return 'Fcil';
    case 'M': return 'Mdio';
    case 'H': return 'Difcil';
    default: return difficulty;
  }
};

export const getNamePdf = (urlPdf) => {
  const lastSlash = urlPdf.lastIndexOf(newLocal);
  return urlPdf.substring(lastSlash + 1);
};
